/* ============================ Main styles ============================ */

$main-color: #ed493c;
$accent-color: #fff1c2;
$green: #a6ce42;
$white: #fff;
$black: #000a18;
$footer-color: #2c3e50;
$text-color: #808080;

::selection {
    background: rgba(#ff0, .3);
}

.flex {
    display: flex;
    align-items: center;
}

.align-right {
    text-align: right;
}

.align-left {
    text-align: left;
}

.align-center {
    text-align: center;
}

img.alignleft {
    float: left;
    margin: 20px;
    margin-left: 0;
}

img.alignright {
    float: right;
    margin: 20px;
    margin-right: 0;
}

img.aligncenter {
    display: block;
    margin: 20px auto;
}

img.alignnone {
    display: block;
    margin: 20px;
}

input,
.btn {
    &:focus,
    &:active {
        outline: none;
    }
}

.padding-0 {
    padding: 0;
}

.bg_white {
    background: $white;
    margin: 0 15px;
}

.product_list_widget:after {
    content: "";
    clear: both;
}

* {
    margin: 0;
    padding: 0;
}
html,
body {
    height: 100%;
}
.super-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.super-content {
    flex: 1 0 auto;
}
.super-footer {
    flex: 0 0 auto;
}

/* ============================ HEADER ============================ */
.header {
    background: $white;
    padding: 20px 15px 0;
    &__button {
        color: $white;
        background: $main-color;
        border: none;
        border-radius: 30px;
        font-family: "Geometria-ExtraLight";
        padding: 12px 20px;
        outline: none;
        text-transform: uppercase;
        font-size: 14px;
        transition: .2s;
        letter-spacing: 2px;
        &:hover {
            background: darken($main-color, 10%);
        }
    }
    .header__top-flex {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding-right: 15px;
    }
    .header__phone {
        font-size: 15px;
        font-family: "OpenSans-Regular";
        margin-right: 10px;
        padding-top: 10px;
        img {
            position: relative;
            margin-top: -4px;
            margin-right: 5px;
        }
        .desc {
            text-align: center;
            color: $text-color;
            font-size: 12px;
            font-family: "Geometria-Light";
        }
    }
    .header__button {
        margin: 0 50px;
    }
    &__cart {
        position: relative;
    }
    &__cart-link {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .header__cart,
    .woocommerce-Price-amount {
        background: none;
        padding: 0;
        font-size: 16px;
        font-family: "Geometria";
        &__img {
            margin-right: 15px;
        }
        &__sum {
            margin-left: 20px;
            position: relative;
            &:before {
                content: "";
                display: block;
                position: absolute;
                left: -20px;
                top: -10px;
                height: 36px;
                width: 1px;
                background: darken($white, 20%);
            }
        }
        &__count {
            margin-right: 17px;
        }
    }
}

/* ============================ Top-nav ============================ */

.top-nav {
    
    position: relative;
    margin-top: 20px;
    &:before {
        content: "";
        display: block;
        position: absolute;
        top: 2px;
        left: 0;
        right: 0;
        height: 1px;
        background: darken($white, 15%);
    }
    ul {
        margin: 0;
        padding: 0;
        position: relative;
        .current_page_parent,
        .current-menu-parent,
        .current-menu-item {
            a {
                background: darken($white, 3%);
                color: $main-color;
                border-color: $main-color;
                text-decoration: none;
            }
        }
        .menu-item-192 {
            a {
                background: none;
                color: $black;
                border-color: transparent;
                text-decoration: none;
            }
        }
        li {
            display: inline-block;
            padding: 0;
            margin: 0;
            margin-right: -4px;
            a {
                text-transform: uppercase;
                color: $black;
                font-family: "Geometria";
                font-size: 17px;
                line-height: 24px;
                padding: 20px 14px;
                display: block;
                border-top: 4px solid transparent;

            }
            ul {
                display: none;
            }
            &:hover {
                a {
                    background: darken($white, 3%);
                    color: $main-color;
                    border-color: $main-color;
                    text-decoration: none;
                }
                ul {
                    padding: 30px;
                    padding-bottom: 10px;
                    background: darken($white, 3%);
                    position: absolute;
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    left: 0;
                    z-index: 10;
                    display: flex;
                    justify-content: space-between;
                    width: 60%;
                    li {
                        display: block;
                        width: 50%;
                        margin-bottom: 10px;
                        clear: both;
                        
                        a {
                            color: $main-color;
                            text-transform: uppercase;
                            font-size: 14px;
                            padding: 0;
                            display: inline;
                            border: none;
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                        ul {
                            padding: 0;
                            background: none;
                            position: relative;
                            display: block;
                            width: 100%;
                            li {
                                margin: 0;
                                padding: 0;
                                display: block;
                                clear: both;
                                width: 100%;
                                a {
                                    color: $black;
                                    text-transform: none;
                                }
                            }
                        }
                    }
                }
            }   
        }
    }
}

body {
    background: $white;
}

body.home {
    background: url("../img/brick.png");
}

/* ============================ MAIN PAGE sec_slider ============================ */

.sec_slider {
    background: $white;
    padding-bottom: 45px;
    &__single-slide {
        height: 430px;
        background-size: cover;
        background-position: center center;
        position: relative;
    }
    &__link {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    .owl-prev {
        position: absolute;
        top: 50%;
        left: 20px;
        height: 31px;
        width: 31px;
        margin-top: -15px;
        background: url("../img/left_arrow.png");
    }
    .owl-next {
        position: absolute;
        top: 50%;
        right: 20px;
        height: 31px;
        width: 31px;
        margin-top: -15px;
        background: url("../img/right_arrow.png");
    }
    .owl-dots {
        text-align: center;
        margin-top: 20px;
    }
    .owl-dot {
        width: 13px;
        height: 13px;
        background: $white;
        border: 1px solid $main-color;
        display: inline-block;
        margin: 5px;
        border-radius: 50%;
        transition: .2s;
    }
    .owl-dot.active {
        background: $main-color;
    }
}

/* ============================ MAIN PAGE sec_about ============================ */

.sec_about {
    background: $white;
    position: relative;
    padding: 0 15px;
    padding-top: 15px;
    &:before {
        content: "";
        position: absolute;
        display: block;
        top: 0;
        left: 15px;
        right: 15px;
        height: 2px;
        background: darken($white, 7%);
    }
    &__main-title {
        color: $black;
        font-size: 30px;
        font-family: "Geometria-Medium";
        text-align: center;
        margin-bottom: 30px;
        text-transform: uppercase;
    }
    &__desc {
        font-size: 20px;
        font-family: "Geometria";
        color: $text-color;
        border-left: 4px solid $main-color;
        padding: 15px;
        padding-left: 30px;
        margin-bottom: 20px;
        p {
            margin: 0;
        }
    }
    &__categories {
        padding-bottom: 70px;
    }
    &__category {
        height: 250px;
    }
    &__cat-title {
        font-family: "Geometria";
        color: $main-color;
        text-transform: uppercase;
        font-size: 20px;
        padding-bottom: 15px;
        border-bottom: 2px solid darken($white, 7%);
    }
    &__sub-cat {
        display: flex;
        align-items: center;
        border-bottom: 2px solid darken($white, 7%);
    }
    &__sub-cat-title {
        max-width: 150px;
        font-size: 15px;
        color: $black;
        font-family: "Geometria";
    }
    &__sub-image {
        margin-right: 40px;
        margin-left: 20px;
        margin-top: 30px;
        margin-bottom: 30px;
        width: auto;
        height: 45px;
    }
    &__parent-link,
    &__child-link {
        color: inherit;
        &:hover {
            color: $main-color;
        }
    }
    .flex {
        flex-wrap: wrap;
    }
}

/* ============================ MAIN PAGE sec_popular ============================ */

.sec_popular {
    background: $white;
    padding: 0 0 90px;
    &__main-title {
        color: $black;
        font-size: 30px;
        font-family: "Geometria-Medium";
        text-align: center;
        margin-bottom: 30px;
        margin: 0;
        padding: 30px 0 60px;
        text-transform: uppercase;
    }
    &__product-wrapper {
        border: 1px solid #d9d9d9;
        margin: 0 3px;
        padding: 15px 30px;
        position: relative;
    }
    &__product-image {
        max-height: 225px;
        width: auto !important;
        position: relative;
        margin: 0 auto;
    }
    &__product-title {
        color: $black;
        font-family: "Geometria";
        font-size: 16px;
        text-transform: uppercase;
    }
    &__product-link {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    .owl-carousel-2 {
        position: relative;
    }
    .owl-prev {
        position: absolute;
        top: 80px;
        left: -65px;
        height: 37px;
        width: 37px;
        margin-top: -15px;
        background: url("../img/left-2.png");
    }
    .owl-next {
        position: absolute;
        top: 68px;
        right: -65px;
        height: 37px;
        width: 37px;
        margin-top: -15px;
        background: url("../img/right-2.png");
    }
    .product_badge_new {
        background-image: url("../img/badge_new.png");
        top: 5px;
        left: 0;
        width: 56px;
        height: 25px;
    }

    .product_badge_sale {
        background-image: url("../img/badge_sale.png");
        top: 5px;
        right: -2px;
        width: 56px;
        height: 25px;
    }

    .product_badge_lider {
        background-image: url("../img/badge_lider.png");
        top: 15px;
        right: 120px;
        width: 60px;
        height: 60px;
    }
}

/* ============================ MAIN PAGE sec_centr ============================ */

body.category .sec_centr:before {
    display: none;
}

.sec_centr {
    position: relative;
    background: $white;
    padding: 0 15px 60px;
    &:before {
        content: "";
        position: absolute;
        display: block;
        top: 0;
        left: 15px;
        right: 15px;
        height: 1px;
        background: darken($white, 10%);
    }
    &__main-title {
        color: $black;
        font-size: 30px;
        font-family: "Geometria-Medium";
        text-align: center;
        margin-bottom: 30px;
        margin: 0;
        padding: 60px 0 60px;
        text-transform: uppercase;
    }
    &__desc {
        font-size: 20px;
        font-family: "Geometria";
        color: $text-color;
        border-left: 4px solid $main-color;
        padding: 15px;
        padding-left: 30px;
        margin-bottom: 20px;
        p {
            margin: 0;
        }
    }
    &__categories {
        margin-bottom: 60px;
    }
    &__cat-wrapper {
        position: relative;
        box-shadow: 0 0 0 0 rgba(91, 91, 91, 0);
        transition: .2s;
        &:hover {
            box-shadow: 12px 7.211px 23.04px 0.96px rgba(91, 91, 91, 0.41);
        }
    }
    &__cat-wrapper {
        padding-bottom: 20px;
    }
    &__cat-title {
        color: $black;
        font-family: "Geometria-Medium";
        text-align: center;
        font-size: 30px;
        padding: 40px 0 60px;
        text-transform: uppercase;
    }
    &__cat-image {
        background-position: center center;
        background-size: cover;
        height: 206px;
    }
    &__subcat-title {
        font-size: 16px;
        font-family: "OpenSans-Regular";
        text-align: center;
        padding: 10px 0;
    }
    &__subcat-desc {
        font-family: "OpenSans-Regular";
        font-size: 12px;
        color: $text-color;
        line-height: 20px;
        padding: 0 30px 25px;
    }
    &__subcat-items {
        padding: 0 20px;
        margin-bottom: 25px;
    }
    &__subcat-item {
        font-family: "OpenSans-Regular", sans-serif;
        font-size: 12px;
        color: $text-color;
        display: flex;
        align-items: center;
        flex-direction: column;
        img {
            margin-right: 10px;
        }
    }
    &__more {
        border: 1px solid $text-color;
        border-radius: 18px;
        color: $text-color;
        text-transform: uppercase;
        padding: 13px 15px;
        font-size: 12px;
        font-family: "OpenSans-Semibold", sans-serif;
        background: $white;
        display: inline;
        margin-left: 20px;
        transition: .2s;
        &:hover {
            text-decoration: none;
            transform: translateY(-3px);
            color: $white;
            border-color: $green;
            background: $green;
        }
    }
    &__call {
        border-radius: 22px;
        color: $white;
        text-transform: uppercase;
        padding: 13px 15px;
        font-size: 12px;
        font-family: "OpenSans-Semibold", sans-serif;
        background: $main-color;
        border: 1px solid $main-color;
        display: inline;
        margin-right: 20px;
        transition: .2s;
        &:hover {
            transform: translateY(-3px);
            border-color: $green;
            background: $green;
        }
    }
    &__link-more {
        color: $text-color;
        font-family: "OpenSans-Semibold", sans-serif;
        font-size: 26px;
        img {
            position: relative;
            margin-top: -2px;
            margin-left: 15px;
            transition: .2s;
        }
        &:hover {
            text-decoration: none;
            color: $text-color;
            img {
                transform: translateX(5px);
            }
        }
    }
    &__inner-top-title {
        color: $black;
        font-family: "Geometria-Medium", sans-serif;
        font-size: 30px;
        margin: 40px 0 70px;
    }
    &__inner-top-image {
        width: 425px;
        height: 555px;
        background-position: center center;
        background-size: cover;
    }
    &__inner-top-subtitte {
        color: $black;
        font-size: 30px;
        font-family: "Geometria-Medium", sans-serif;
        margin-bottom: 30px;
        padding-left: 40px;
    }
    &__inner-top-text {
        padding: 0 40px;
        font-size: 18px;
        color: $text-color;
        font-family: "Geometria", sans-serif;
        line-height: 24px;
    }
    &__inner-cat-title {
        font-size: 30px;
        font-family: "Geometria-Medium", sans-serif;
        color: $black;
        margin: 120px 0 80px;
        text-transform: uppercase;
    }
}

.inner_centr {
    height: 515px;
    background-image: url("../img/girl_sec_centr__inner.png");
    background-size: cover;
    background-position: left bottom;
    padding-top: 63px;
    &__title {
        font-size: 30px;
        font-family: "Geometria-Medium", sans-serif;
        color: $black;
        margin-top: 25px;
    }
    &__subtitle {
        font-size: 18px;
        font-family: "Geometria-Medium", sans-serif;
        color: $black;
        margin-bottom: 40px;
    }
    &__address {
        color: $text-color;
        font-size: 14px;
        font-family: "Geometria-Light", sans-serif;
        margin-bottom: 18px;
        span {
            width: 16px;
            height: 16px;
            text-align: center;
            margin-right: 15px;
            display: inline-block;
        }
        img {
            position: relative;
        }
    }
    &__form {
        padding-right: 40px;
        &-bg {
            padding: 30px;
            background: $footer-color;
            color: $white;
            font-size: 14px;
            font-family: "Geometria", sans-serif;
            line-height: 24px;
        }
        input {
            width: 100%;
            border: none;
            background: transparent;
            padding: 5px 10px;
            border-bottom: 1px solid $text-color;
            margin-bottom: 30px;
            &::placeholder {
                color: $white;
                font-size: 14px;
                font-family: "Geometria", sans-serif;
                line-height: 24px;
                text-transform: uppercase;
            }
        }
        input[type="submit"] {
            background: $main-color;
            border-radius: 30px;
            padding: 15px 35px;
            text-align: center;
            margin-top: 40px;
            border: none;
            width: auto;
            color: $white;
            text-transform: uppercase;
            font-size: 14px;
            font-family: "Geometria", sans-serif;
            transition: .2s;
            &:hover {
                background: $green;
            }           
        }
    }
}

/* ============================ team ============================ */

.team {
    background: $white;
    padding: 70px 0;
    &__title {
        font-family: "Geometria-Medium";
        color: $black;
        font-size: 30px;
        margin-bottom: 70px;
        text-transform: uppercase;
    }
    &__wrapper {
        background: #f7f7f7;
        position: relative;
        padding-top: 20px;
    }
    &__name {
        color: $black;
        text-transform: uppercase;
        font-family: "Geometria-Medium";
        font-size: 30px;
        margin: 0;
        margin-left: 25px;
        margin-top: 20px;
    }
    &__position {
        color: $text-color;
        font-size: 14px;
        font-family: "Geometria";
        margin-left: 25px;
        display: inline-block;
        max-width: 150px;
    }
    &__photo {
        background-position: center center;
        background-size: cover;
        width: 265px;
        height: 365px;
        position: relative;
        z-index: 1;
        transition: .2s;
        overflow: hidden;
        &:after {
            content: "";
            transition: .2s ease-in-out;
            display: block;
            position: absolute;
            left: 0;
            height: 200px;
            bottom: 0;
            right: 0;
            background: linear-gradient(-180deg, rgba($main-color, 0), $main-color);
            transform: translateY(200px);
        }
    }
    &__link {
        &:hover {
            text-decoration: none;
            .team__photo {
                &:after {
                    transform: translateY(0px);
                }
            }
        }
    }
    &__desc {
        background: #f7f7f7;
        padding: 40px 30px 80px;
        transition: .2s;
    }
    &__position-2 {
        position: relative;
        display: block;
        &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: -40px;
            height: 4px;
            width: 130px;
            background: $main-color;
        }
    }
    &__name-2 {
        max-width: 150px;
    }
    &__desc-text {
        color: $text-color;
        font-size: 18px;
        font-family: "Geometria";
        line-height: 31px;
    }
}

.zoom {
    background: $white;
    padding-top: 100px;
    &__title {
        font-family: "Geometria-Medium";
        font-size: 30px;
        text-transform: uppercase;
        color: $black;
        margin: 0 0 100px;
    }
}

/* ============================ subscription ============================ */

.subscription {
    background: $white;
    padding: 140px 0;
    margin-left: -15px;
    width: calc(100% + 30px);
    &__wrapper {
        background-image: url("../img/subscription.png");
        background-size: cover;
        background-position: center center;
        padding: 70px 0 60px;
    }
    &__desc {
        color: $white;
        font-size: 24px;
        font-family: "Geometria-Medium";
        text-transform: uppercase;
        padding-left: 60px;
    }
    &__email,
    &__submit {
        background: $white;
        color: $text-color;
        border: none;
        padding: 20px 25px 17px;
        font-size: 18px;
        font-family: "Geometria";
        text-transform: uppercase;
    }
    &__email {
        border-radius: 30px 0 0 30px;
        &::placeholder {
            color: $text-color;
            font-size: 18px;
            font-family: "Geometria";
            text-transform: uppercase;
        }
    }
    &__submit {
        border-radius: 0 30px 30px 0;
        margin-left: -4px;
        position: relative;
        &:before {
            content: "";
            display: block;
            position: absolute;
            top: 6px;
            left: 0;
            width: 3px;
            height: 50px;
            background: rgba(128, 128, 128, 0.28);
        }
    }
}

/* ============================ video ============================ */

.video {
    background: $white;
    padding: 15px 0 120px;
    &__title {
        color: $black;
        font-size: 24px;
        font-family: "Geometria-Medium";
        position: relative;
        max-width: 330px;
        margin-left: 40px;
        display: inline-block;
        text-transform: uppercase;
        &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: -40px;
            left: 0;
            height: 3px;
            width: 135px;
            background: $main-color;
        }
    }
    &__desc {
        color: $text-color;
        font-family: "Geometria";
        font-size: 18px;
        line-height: 31px;
        margin-top: 60px;
        max-width: 330px;
        margin-left: 40px;
        display: inline-block;
    }
    &__youtube {
        color: $text-color;
        font-size: 24px;
        font-family: "Geometria";
        text-decoration: underline;
        margin-top: 40px;
        display: inline-block;
    }
    &__link {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -48px;
        margin-left: -48px;
        width: 97px;
        height: 97px;
    }
}

body.category-educational_center .video {
    margin-top: 80px;
}

/* ============================ edu-center ============================ */

.edu-center {
    background: $white;
    padding: 40px 0;
    &__maintitle {
        font-family: "Geometria-Medium";
        font-size: 30px;
