.blog-list {
    padding: 40px 0;
    &__maintitle {
        font-size: 30px;
        font-family: "Geometria";
        text-transform: uppercase;
        color: $black;
        margin-bottom: 50px;
    }
    article {
        margin-bottom: 60px;
    }
    &__text {
        color: $black;
        font-family: "Geometria-Light";
        line-height: 24px;
        font-size: 16px;
    }
    &__date {
        color: $text-color;
        font-family: "Geometria-Light";
        line-height: 40px;
        font-size: 16px;
    }
    &__title {
        font-family: "Geometria-Medium";
        color: $black;
        text-transform: uppercase;
        line-height: 26px;
        font-size: 20px;
        margin-bottom: 0;
    }
    &__link {
        text-transform: uppercase;
        color: $text-color;
        font-size: 16px;
        border: 3px solid #b3bfb1;
        border-radius: 30px;
        padding: 10px 50px 7px;
        display: inline-block;
        margin-top: 50px;
        margin-bottom: 0;
        transition: .2s;
        &:hover {
            color: $main-color;
            border-color: $main-color;
            text-decoration: none;
        }
    }
    img.blog-list__main-image {
        margin-top: 0;
        margin-right: 65px;
        margin-bottom: 60px;
    }
}

/* ============================ single_beauty-centr ============================ */

.single_beauty-centr {
    &__title {
        font-size: 20px;
        font-family: "OpenSans-Semibold";
        color: $black;
        margin-bottom: 40px;
        margin-top: 0;
    }
    &__excerpt {
        color: $text-color;
        font-family: "OpenSans-Regular";
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 60px;
    }
    &__subcat-items {
        padding: 0 0 60px;
    }
    &__subcat-item {
        color: $text-color;
        font-family: "OpenSans-Regular";
        font-size: 16px;
        img {
            margin-right: 15px;
        }
    }
    &__button {
        background: $main-color;
        color: $white;
        text-transform: uppercase;
        border: none;
        border-radius: 30px;
        padding: 12px 55px;
    }
    &__text {
        padding: 80px 0;
        font-size: 18px;
        font-family: "Geometria";
        color: $text-color;
    }
    &__link {
        color: $main-color;
        border: 3px solid $main-color;
        text-transform: uppercase;
        font-size: 16px;
        font-family: "Geometria";
        padding: 12px 50px 9px;
        border-radius: 30px;
        transition: .2s;
        margin-top: 60px;
        position: relative;
        display: inline-block;
        margin-bottom: 30px;
        &:hover {
            background: $main-color;
            text-decoration: none;
            color: $white;
        }
    }
}

/* ============================ widget-woof ============================ */

.widget-woof {
    padding-right: 20px;
    border-right: 1px solid #dfdfdf;
    h4 {
        color: $black;
        font-size: 14px;
        font-family: "Geometria";
    }
    .woof_container_inner {
        border-top: 1px solid #dfdfdf;
    }
    .woof_price3_search_container .woof_container_inner {
        border: none;
    }
    .icheckbox_minimal-red {
        background: #dfdfdf;
    }
    .icheckbox_minimal-red, .iradio_minimal-red {
        width: 14px;
        height: 14px;
    }
    .icheckbox_minimal-red.checked {
        position: relative;
        &:after {
            position: absolute;
            content: "";
            display: block;
            top: 1px;
            left: 0;
            height: 11px;
            width: 14px;
            background: url("../img/ckeked.png") no-repeat;
        }
    }
    .irs-line {
        height: 6px;
        top: 28px;
    }
    .irs-bar {
        height: 6px;
        top: 28px;
        background-position: 0 -60px;
    }
    ul {
        padding: 0;
        li {
            &:hover {
                cursor: pointer;
                color: $main-color;
            }
        }
    }
    label {
        font-family: "Geometria-Light";
        font-size: 12px;
        color: $black;
        padding: 0;
        padding-top: 7px;
        margin-top: 5px;
        transition: .2s;
        &:hover {
            cursor: pointer;
            color: $main-color;
        }
    }
}

.product_list_widget .one_product {
    margin-bottom: 60px;
}

.woof_products_top_panel li span {
    color: $black;
    font-family: "Geometria";
    font-size: 12px;
    background: url("../img/filter_delete.png");
    background-size: 10px 10px;
    background-repeat: no-repeat;
    background-position: right;
    transition: .2s;
    &:hover {
        background-size: 10px 10px;
        transform: rotate(360deg);
    }
}

/* ============================ page-about ============================ */

.page-about {
    &__title {
        font-size: 30px;
        font-family: "Geometria";
        color: $black;
        margin-bottom: 50px;
        text-transform: uppercase;
    }
    &__text {
        color: $text-color;
        margin-top: 70px;
        font-size: 18px;
        font-family: "Geometria";
    }
}

/* ============================ partners ============================ */

.partners {
    margin: 80px 0;
    &__title {
        color: $black;
        text-transform: uppercase;
        font-family: "Geometria-Medium";
        font-size: 30px;
        margin-bottom: 80px;
    }
}

/* ================================================================== */
/* ============================ SlickNav ============================ */
/* ================================================================== */

.slicknav_menu {
    display: none;
}

